@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap');

html {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  background-color: #1E212A;
}
.link {
  color: #F8BD33;
  text-decoration: none;
}

.App { 
  font-family: 'Poppins', sans-serif;  
}

nav {
  display: grid;
  grid-template-columns: 2fr 8fr;
  /* background: #220AAD; */ 
  align-items: center;
}
nav ul {
  display: flex;
  list-style-type: none;
  justify-content: flex-end;
  padding-inline-start: 0;
}
nav a {
  margin: .3em;
  font-size: .8em;
  font-weight: 600;
  color: #fff;
  text-decoration: none;
  
}
.logo {
  font-size: 1.2em;
  display: flex;
  align-items: center;
  color: #F8BD33;
  margin: 1em;
  font-family: 'Fredoka One', cursive;
  
}
.container {
  justify-content: center;
  display: flex;
}
.verticalcenter {
  display: flex;
  align-items: center;
}

/* ////////////// LOGIN ////////////// */
.login-form {
  color: #fff;
  display: flex;
  flex-direction: column;
  margin: 1em auto;
  padding: 1em;
  width: 80%;
}
.login-form input {
  padding: 1em;
  border-radius: 15px;
  margin: 1em;
}
.login-form label {
  
  margin: .3em 0 0 1em;
  color: #F8BD33;
}
.form-btn {
  color: #F8BD33;
  background-color: #282a35;
  width: 50%;
  padding: 1em;
  border: 1px solid #F8BD33;
  border-radius: 15px;
  margin: 1em auto;
  cursor: pointer;
}
.form-btn:hover {
  color: #c6a049;
  background-color: #3A3C48;
  border: 1px solid #c6a049;
  
  
}
.login-form-signup{
  margin: 1em;
  text-align: center;
  color: #F8BD33;
  text-decoration: none;
}


/* /// Exercices /// */
.exercices-container h1 {
  text-align: center;
  color: #fff;
  font-size: 1.5em
}
.exercices {
  color: #fff;
  display: flex;
  justify-content: flex-end;
  margin: 0 1em;
}

.exercices-add-button {
  display: flex;
  align-items: center;
  margin-left: 1em;
}
.exercices-list {
  text-transform: uppercase;
  color: #fff;
  padding: 1em;
  display: flex;
  justify-content: space-between;
}
.exercices-border {
  margin: 0 auto;
  border-bottom: .5px solid #12C380;  
}
/* /// WORKOUTS /// */
.workouts-container {
  color: #fff;
  margin: 0 auto;
  padding: 0 1em 2em 1em;
  background-color: #2F313E;  
}
.workouts {
  display: grid;
  grid-template-columns: 2fr 8fr 2fr;
  padding: 1em;
  background-color: #3A3C48;
  border-radius: 20px; 
}
.workouts-exercices {
  text-align: center;
}
.workouts-exercice-number {
  text-align: center;
  color: #F8BD33;
  font-weight: bold;
}
.workouts-header {
  display: flex;
  color: #fff;
  justify-content: space-between; 
}
.workouts-title {
  padding: 1.5em;
  font-weight: bold;
  text-transform: capitalize;
}

.workouts-add {
  text-align: right;
  margin: 1em;
}
.wave-container {
  position: relative;
  background: url('./assets/image/1315.jpg');
  background-size: 100%;
  background-position: 0px -25px;
  /* background-image: linear-gradient(to left bottom,  #0F4C81,  #12C380, #F8BD33); */
  color: #FFF;
  overflow: hidden;
  border-radius: 30px;
  margin: 2em auto;
  padding-top: 3em;
  box-shadow: 0px 1px 3px 2px #282a35;             
}
.wave-container > svg {
  display: block;
}

/* /// WORKOUT /// */
.workout-container {
  background-color: #282a35;
  border-radius: 30px;
  margin: 2em auto;
  padding: 2em 0 .7em 0;
}
.dumbbell {
  color: #F8BD33;
}
.workout-title h4 {
  margin: 0;
  color: #ebe9e9;
  text-transform: capitalize; 
}
.workout-add {
  width: 80%;
  margin: 0 auto;

}


.workout-name {
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  text-align: center;
  color: #F8BD33;
  font-weight: 600;
}

/* /// WORKOUT DETAILS /// */
.workout-details-container {
  font-size: 1.2em;
  background-color: #3A3C48;
  color: #FFF;
  border-radius: 30px;
  margin: 1em;
  padding: 1em;
}
.workout-details-1 {
  display: flex;
  justify-content: space-between;
  margin: .5em 0;  
}

.workout-details-title {
  text-transform: capitalize;
  margin: 0; 
}
.workout-details-2 {
  display: flex;
  justify-content: space-between;
  
}
.workout-details-3 {
  display: flex; 
}
.workout-series {
 color: #F8BD33;
}
.workout-series-undo {
  color: #ae8e43dd;
}
.workout-series-span {
  color: #FFF;
}
.workout-details-increment {
  display: flex;
}

/* ADD Exercice to workout FORM */

.add-exercice {
  color: #fff;
  padding: .5em;
  border-radius: 10px;
  font-size: 1.2em;
  display: flex;
  justify-content: flex-end;

}
.add-exercice-text {
  color: #fff;
  display: none
}
.add-exercice-form {
  color: #fff;
  color: #fff;
  background-color: #282a35;
  display: flex;
  flex-direction: column;
  margin: .5em auto;
}
.add-exercice-form select {
  
  margin: 1em 0;
  padding: 1em;
  color: #fff;
  background-color: #282a35;
  outline: none;
}
.add-exercice-form select:focus {
  
  border-color: #F8BD33;
}
.add-exercice-form button {

  margin: 0 auto;
  width: 50%;
  padding: 1em;
  color: #F8BD33;
  background-color: #282a35;
}
.add-exercice-form-open {
  display: none;
}

/* /// Create a New Exercice Form /// */
.form-new-exercice {
  display: flex;
  flex-direction: column;
}
/* /// NEW WORKOUT /// */

.new-workout-title-container {
  display: flex;
  flex-direction: column;
  padding: 0 1em;
}
.new-workout-exercices-container {
  display: flex;
  justify-content: space-between;
  
}
.new-workout-exercices-title {
  font-size: large;
  font-weight: bold;
}
.new-workout-container { 
  display: flex;
  flex-direction: column;
  padding: 1em 0;
  color: #fff;
  background-color: #282a35;
  border-radius: 20px;
  height: 80vh;   
}
.new-workout-title {
  font-size: 1.2em;
  font-weight: 700;
  text-align: center;
  border: none;
  background-color: #282a35;
  color: #fff;
  padding: .5em;
  outline: none;
}
.new-workout-datas {
  display: grid;
  grid-template-columns: 6fr 3fr 3fr;
  font-size: .8em;
  text-align: right;
  border-bottom: 1px solid #F8BD33;
  padding: .5em 0;
}
.new-workout-name {
 
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: left;
  
}
.new-workout-select {
  display: flex;
  flex-direction: column;
  font-size: .8em;
  
}
.new-workout-select span {
  font-size: 1.2em;
}
.new-workout-input{
  color: #FFF;
  border-radius: 5px;
  padding: .5em 1em;
  background-color: #282a35;
  margin: .3em 0 1em 0;
  outline: none;
}
.new-workout-input{
  color: #FFF;
  border-radius: 5px;
  padding: .5em 1em;
  background-color: #282a35;
  margin: .3em 0 1em 0;
  outline: none;
}
.new-workout-input:focus{
 border-color:  #F8BD33;
}
 .new-workout-addform-hidden {
   
   display: none;
 }
 .new-workout-addform {
  /* background-color: #232E4C; */
  padding:  0 1em;
  margin: 0  0 1em 0;
  border-radius: 15px;
}
.new-workout-exercices-list {
  background-color: #2F313E;
  padding: 1em;
  margin: 1em 0;
  border-radius: 5px;
  
}
 .new-workout-datas-container {
   padding: 1em;
  /* background-color:  #232E4C; */
  border-radius: 20px;
  
 }
 .new-workout-add {
 display: flex;
 font-size: .8em;
 align-items: center;
 color: #F8BD33;
 cursor: pointer;
}
.new-workout-warning {
  display: flex;
  font-size: .8em;
  align-items: center;
  
 }
.new-workout-addbtn-container{
  display: flex;
  flex-direction: row-reverse;
}
.new-workout-addbtn {
  font-weight: bold;
  color: #2F313E;
  padding: .5em;
  border-radius: 5px;
  background:  #F8BD33;
  border: .5px solid #2F313E; 
  cursor: pointer; 
        
}
.new-workout-submitbtn {
  font-weight: bold;
  margin: 0 auto;
  color: #F8BD33;
  padding: .5em;
  text-align: center;
  border-radius: 5px;
  background: #2F313E;
  border: .5px solid #F8BD33;
  cursor: pointer;           
}
.new-workout-submitbtn:hover {
  color: #c6a049;
  background: #3A3C48;
  border: .5px solid #c6a049;            
}
.loginfo {
  text-align: center;
  margin-bottom: 1em;
}
#reset {
  display: none;
}

@media screen and (min-width: 480px) {
  .wave-container {
    background-size: 100%;
    background-position: 0px -35px;
  }
  nav a { 
    
    font-size: 1em;
  }
  nav { 
    margin: 0 1em;
    
  }
}

@media screen and (min-width: 768px) {
  .workouts-list { 
    display: grid;
    grid-template-columns: 5fr 5fr;
    color: #fff;
    margin: 0 auto;
    padding: 0 1em 2em 1em;
    background-color: #2F313E;  
  }
  .wave-container {
    background-position: 0px -35%;
    background-size: 100% 80%;          
  }
  .workout {
    display: grid;
    grid-template-columns: 5fr 5fr;
    border-radius: 30px;
    margin: 2em auto;
    padding: 2em 0 .7em 0;
  }
  .signup-form-text {
    margin: 0 auto;
    width: 40%;
  }
  .add-exercice-text {
    color: #fff;
    display: inline;
  }
}